export default {
  layout: "default",
  data() {
    return {
      bottomCards: {},
      nextDataLink: "",
      cover: [],
      home: [],
      seoObject: {}
    };
  },
  head() {
    return {
      title: this.seoObject.page_title,
      meta: [
        {
          hid: "description",
          name: "description",
          content: this.seoObject.description
        },
        {
          hid: "keywords",
          name: "keywords",
          content: this.seoObject.meta_keyword
        },
        {
          hid: "twitter:card",
          name: "twitter:card",
          content: this.seoObject.page_title
        },
        {
          hid: "twitter:title",
          name: "twitter:title",
          content: this.seoObject.page_title
        },
        {
          hid: "twitter:description",
          name: "twitter:description",
          content: this.seoObject.description
        },
        {
          hid: "og:title",
          property: "og:title",
          content: this.seoObject.page_title
        },
        {
          hid: "og:type",
          property: "og:type",
          content: "article.text"
        },
        {
          hid: "og:url",
          property: "og:url",
          content: ""
        },
        {
          hid: "og:description",
          name: "og:description",
          content: this.seoObject.description
        },
        {
          hid: "og:image",
          name: "og:image",
          content: this.seoObject.meta_image
        }
      ]
    };
  },
  async fetch() {
    var self = this;
    await self.$axios
      .$get(process.env.baseUrl + "/s/h/BlogPage")
      .then(function(posts) {
        self.seoObject = posts;
      })
      .catch(function(error) {
        console.log(error);
      });
    await self.$axios
      .$get(process.env.baseUrl + "/blog/api/v1/")
      .then(function(posts) {
        self.bottomCards = posts.results;
        self.nextDataLink = posts.next;
      })
      .catch(function(error) {
        console.log("No Net" + error);
      })
      .finally(function() {});

    await self.$axios
      .$get(process.env.baseUrl + "/blog/api/v1/cover")
      .then(function(posts) {
        self.cover = posts.results;
      })
      .catch(function(error) {
        console.log("No Net" + error);
      })
      .finally(function() {});
    await self.$axios
      .$get(process.env.baseUrl + "/blog/api/v1/blog_home")
      .then(function(posts) {
        self.home = posts.results;
      })
      .catch(function(error) {
        console.log("No Net" + error);
      })
      .finally(function() {});
  },
  methods: {
    async loadData() {
      if (this.nextDataLink != null) {
        var self = this;
        await self.$axios
          .$get(self.nextDataLink)
          .then(function(posts) {
            posts.results.forEach(element => {
              self.bottomCards.push(element);
            });
            self.nextDataLink = posts.next;
          })
          .catch(function(error) {
            console.log("No Net" + error);
          })
          .finally(function() {});
      } else {
        // alert("Null");
      }
    }
  }
};
